import React,{ lazy } from 'react'
import {
  CRow,
  CCol,
  CWidgetIcon,
  CButton,
  CForm,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CContainer,
  CImg,
  CPopover,
  CSpinner,
  CLabel,
  CFormGroup
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { numberFormat } from '../../numberFormat';

class DetailTrx extends React.Component {
  constructor(props) {
    super(props);
    // this.state = {hf_username: '',hf_password:''};
    // this.handleChange = this.handleChange.bind(this);
    this.state = {
        error: null,
        isLoaded: false,
        items: [],
      };
   }

   componentDidMount() {
    fetch("http://mj-infomatika.my.id/transaksi/transaksiByUser_detail/2")
    .then(res => res.json())
       .then(
         (result) => {
           this.setState({
             isLoaded: true,
             items: result.data
           });
           
         },
         (error) => {
           this.setState({
             isLoaded: true,
             error
           });
         }
       )
    }
   

    
  render() {
    const {items,isLoaded } = this.state;

    const content = items.map((post) =>
    <CCol xs="6"  key={post.id}>
      <CCard color={post.posisi=='3'?'info':post.posisi==2?'warning':'success'} className="text-white">
          <CCardBody  style={{padding:5,fontSize:10}}>
            <CFormGroup>
                <CLabel  style={{marginBottom:"0.1rem",textAlign:"left",display:"block",fontWeight:"bold"}} htmlFor="company">{post.kode_transaksi}</CLabel>
                <CLabel  style={{marginBottom:"0.1rem",textAlign:"left",display:"block"}} htmlFor="nf-email">{"Order: "+post.tanggal}</CLabel>
                <CLabel  style={{marginBottom:"0.1rem",textAlign:"left",display:"block"}} htmlFor="nf-email">{post.kd_barang}</CLabel>
                <CLabel  style={{marginBottom:"0.1rem",textAlign:"left",display:"block"}} htmlFor="nf-email">{post.nama_barang}</CLabel>
                <CLabel  style={{marginBottom:"0.1rem",textAlign:"left",display:"block"}} htmlFor="nf-email">{post.keterangan}</CLabel>
                <CLabel  style={{marginBottom:"0.1rem",textAlign:"center",display:"block",fontWeight:"bold"}} htmlFor="nf-email">{"bid by -> "+post.username}</CLabel>
            </CFormGroup>
          </CCardBody>
          <CCardFooter style={{fontSize:10}} color={post.posisi=='3'?'info':post.posisi==2?'warning':'success'} className="text-white text-center" data-id={post.id} data-value={post.keterangan} data-value={post.harga} onClick={this.acceptReq}>
          <CLabel  style={{marginBottom:"0.1rem",textAlign:"center",display:"block",fontWeight:"bold"}} htmlFor="nf-email">{post.posisi=='3'?'done':post.posisi==2?'on proses':'hold'}</CLabel>
            {numberFormat(post.harga)}
          </CCardFooter>
      </CCard>
    </CCol>
  );


  return (
    <CContainer fluid>
      <CRow>
          {isLoaded==false?<CSpinner style={{ position: "fixed", top: "50%", left: "50%" }} color="info" />:null}
          {content}
      </CRow>
      </CContainer>
        
      
    
  )
}
}

export default DetailTrx
